*{
    font-family: Helvetica, Arial, sans-serif;
}

body {
    padding: 60px;
}

input {
    color: grey;
    border: 1px solid rgb(209, 209, 209);
    border-radius: 6px;
    font-size: 1.2rem;
    width: 500px;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    
}

.card{
    align-content: center;
    background-color: rgb(209, 209, 209);
    border: 1px solid rgb(209, 209, 209);
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img {
    max-height: 170px;
}
